/*
some nice typographical defaults
more here: http://www.newnet-soft.com/blog/csstypography
*/
/******************************************************************
H1, H2, H3, H4, H5, H6 STYLES
******************************************************************/
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5 {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  text-rendering: optimizelegibility;
  font-weight: 400;
  color: #000;
  margin-top: 0; }
  h1 a, .h1 a,
  h2 a, .h2 a,
  h3 a, .h3 a,
  h4 a, .h4 a,
  h5 a, .h5 a {
    text-decoration: none; }

h1, .h1 {
  font-size: 29.0304px; }
  @media (min-width: 400px) and (max-width: 1200px) {
    h1, .h1 {
      font-size: calc( 29.0304px + 37.39973 * ( ( 100vw - 400px) / 800 )); } }
  @media (min-width: 1200px) {
    h1, .h1 {
      font-size: 66.43013px; } }

h2, .h2 {
  font-size: 24.192px; }
  @media (min-width: 400px) and (max-width: 1200px) {
    h2, .h2 {
      font-size: calc( 24.192px + 25.0155 * ( ( 100vw - 400px) / 800 )); } }
  @media (min-width: 1200px) {
    h2, .h2 {
      font-size: 49.2075px; } }

h3, .h3 {
  font-size: 20.16px; }
  @media (min-width: 400px) and (max-width: 1200px) {
    h3, .h3 {
      font-size: calc( 20.16px + 16.29 * ( ( 100vw - 400px) / 800 )); } }
  @media (min-width: 1200px) {
    h3, .h3 {
      font-size: 36.45px; } }

h4, .h4 {
  font-size: 16.8px; }
  @media (min-width: 400px) and (max-width: 1200px) {
    h4, .h4 {
      font-size: calc( 16.8px + 10.2 * ( ( 100vw - 400px) / 800 )); } }
  @media (min-width: 1200px) {
    h4, .h4 {
      font-size: 27px; } }

h5, .h5 {
  font-size: 16.8px; }
  @media (min-width: 400px) and (max-width: 1200px) {
    h5, .h5 {
      font-size: calc( 16.8px + 10.2 * ( ( 100vw - 400px) / 800 )); } }
  @media (min-width: 1200px) {
    h5, .h5 {
      font-size: 27px; } }

h6, .h6 {
  font-size: 14px; }
  @media (min-width: 400px) and (max-width: 1200px) {
    h6, .h6 {
      font-size: calc( 14px + 6 * ( ( 100vw - 400px) / 800 )); } }
  @media (min-width: 1200px) {
    h6, .h6 {
      font-size: 20px; } }

